import React from "react"
import Layout from "../../Layout/Layout"
import styled from "styled-components"
import { Container, Row, Col, Card, Image } from "react-bootstrap"

const BlogDetailed = ({ children }) => {
  return (
    <Layout>
      <Blogdetailedwrapper>
        <Container className="blogDetailed">
          <Row>
            <Col lg={8}>
              <Card>
                <Image
                  rounded
                  fluid
                  src="https://github.blog/wp-content/uploads/2022/07/image1-1.png"
                ></Image>
                <Card.Body>
                  <div className="d-flex flex-row align-items-center autanddate">
                    <Image
                      fluid
                      height={40}
                      width={40}
                      roundedCircle={true}
                      thumbnail={true}
                      src="https://avatars.githubusercontent.com/u/54428228"
                    ></Image>
                    <h3>Akhil S Kumar</h3>
                    <h3>• Posted on 29 Dec 2022</h3>
                  </div>
                  <div className="title">
                    <h1>Hoppscotch raises $3M in seed funding</h1>
                  </div>
                  <div className="category">
                    <h5>#Open Source</h5>
                  </div>
                  <div className="mdx">
                    {children}
                  </div>
                </Card.Body>
              </Card>
            </Col>
          </Row>
        </Container>
      </Blogdetailedwrapper>
    </Layout>
  )
}

export default BlogDetailed

const Blogdetailedwrapper = styled.div`
  background-color: #111111;
  font-family: "Montserrat", sans-serif;
  .blogDetailed {
    padding: 20px 0px 100px 0px;
    .card {
      background-color: #1a1a1a;
      border-radius: 10px;
      margin-top: 50px;
      .card-body {
        margin: 10px 50px 10px 50px;
        .autanddate{
          h3 {
            margin-left: 10px;
            font-size: 15px;
            color: #FAFAFA;
          }
        }
        .title{
          margin-top: 30px;
          h1{
            font-size: 45px;
            font-weight: 600;
            color: #FAFAFA
          }
        }
        .category{
          margin: 20px 0px 40px 0px;
          h5 {
            background-image: linear-gradient(to right, #0d8add, #15d7c8);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            font-size: 15px;
          }
        }
        .mdx{
          color: #FAFAFA;
        }
      }
    }
  }
`
